@import "../../../styles/root";

.skills {
    width: 100%;
    background-color: rgb(255, 255, 255);
    display: flex;
    flex-direction: column;
    align-items: center;

    .title {
        text-align: center;
        font-size: 3.9rem;
        font-weight: 700;
        line-height: 5rem;
        @include linerText(45deg, rgb(104, 40, 51), rgb(167, 25, 44));
    }

    .categories {
        padding: 0;
        font-size: 1.7rem;
        list-style: none;
        display: flex;
        justify-content: center;
        flex-wrap: wrap;

        .item {
            box-shadow: 0 0.1rem 1.5rem -0.3rem rgb(112, 145, 161);
            padding: 1rem;
            margin: 1rem;
            border-radius: 2rem;
            cursor: pointer;
            text-transform: uppercase;

            &:hover {
                background: rgb(119, 210, 226);
                color: $white-color;
            }
        }

        .active {
            background: rgb(119, 210, 226);
            color: $white-color;
        }
    }

    .technologies {
        padding: 0;
        width: 80%;
        font-size: 1.5rem;
        list-style: none;
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        animation: growth ease-in 0.5s;
        will-change: opacity, transform;

        .item {
            width: 35rem;
            height: 35,9rem;
            margin: 2rem;
            box-shadow: 0 0.1rem 1.5rem -0.3rem #7091a1;
            box-sizing: border-box;
            padding: 1rem;
            border-radius: 0.5rem;

            .boxSymbol {
                width: 100%;
                height: 75%;
                background-color: rgb(255, 255, 255);
                display: flex;
                align-items: center;

                .symbol {
                    width: inherit;
                    height: inherit;
                    color: rgb(50, 62, 77);
                }
            }

            .name {
                width: 100%;
                height: 10%;
                line-height: 3rem;
                font-size: 2.3rem;
                font-weight: 600;
                text-align: center;
            }

            .details {
                width: 100%;
                height: 15%;
                display: flex;
                align-items: center;
                justify-content: center;
                opacity: 0.7;
            }
        }
    }
}
