@import "../../../styles/root";

.ourTeam {
    font-family: "roboto", sans-serif;
    width: 100%;
    height: 100vh;
    box-sizing: border-box;
    padding: 64px 48px;
    color: $text-color;
    background-color: rgb(248, 249, 250);

    .title {
        text-align: center;
        font-size: 32px;
        font-weight: 700;
        line-height: 50px;
        @include linerText(45deg, #9c0606, #02ccda);
    }

    .listMember {
        width: 100%;
        height: 600px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;

        .memberCard {
            width: 325px;
            height: 535px;
            margin: 0 60px;
            padding: 20px;
            display: flex;
            flex-direction: column;
            border-radius: 10px;
            position: relative;

            &:hover {
                background-color: rgba(226, 230, 236, 0.4);
                box-shadow: 0px 1px 27px -3px rgb(0 0 0 / 20%);

                .boxIconTrash {
                    display: block;
                }
            }

            .cardImg {
                width: 100%;
                height: 100%;
                display: flex;
                justify-content: center;
                align-items: center;

                .image {
                    height: 280px;
                    width: 280px;
                    box-sizing: border-box;
                    background: rgba(255, 255, 255, 0.3);
                    border-radius: 500px;
                    border: 8px solid rgba(208, 213, 221, 0.8);
                }

                .iconUser {
                    padding: 15px;
                    color: rgb(31, 62, 77);
                }
            }

            .cardContent {
                box-sizing: border-box;
                width: 100%;
                height: calc(100% - 64%);
                padding: 5px;

                .name {
                    display: block;
                    margin: 5px 0 0;
                    width: 100%;
                    line-height: 25px;
                    text-align: center;
                    color: black;
                    font-size: 20px;
                    font-weight: 700;
                    cursor: pointer;
                    text-decoration: none;
                }

                .job {
                    margin: 10px 0;
                    width: 100%;
                    line-height: 25px;
                    text-align: center;
                    box-sizing: border-box;
                    font-size: 16px;
                }

                .listIconsSocial {
                    text-align: center;
                    font-size: 19px;
                    .icon {
                        background: $black-color;
                        color: $white-color;
                        padding: 10px;
                        margin: 9px;
                        border-radius: 20px;
                        cursor: pointer;
                    }
                    a {
                        color: $text-color;
                    }
                }
            }
        }
    }
}

.addMember {
    width: 325px;
    height: 535px;
    margin: 0 60px;
    padding: 20px;
    display: flex;
    flex-direction: column;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    // box-shadow: 0px 1px 27px -3px rgb(0 0 0 / 20%);

    .box {
        width: 60px;
        height: 60px;
        background: #63b1bb;
        border-radius: 100px;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 3.5rem;
        color: rgb(248, 249, 250);
        box-shadow: 0px 1px 27px -3px rgb(0 0 0 / 20%);
        cursor: pointer;

        &:hover {
            box-shadow: 0px 1px 27px -3px rgba(0, 255, 255, 0.6);
        }

        &:active {
            width: 95px;
            height: 95px;
        }
    }
}

